export default {
	data () {
		return {
			serverTime: 0
		}
	},
	
	methods: {
		reBuyTicket (item) {
		  return {
		    title: '',
		    show: true,
		    action: () => {
				this.$push(this.$mRouter.scenicDetail({
					scenicId: item.ord_scenicId
				}))
		    }
		  }
		},
		action1Click (index) {
			this.list[index.index].stateModel.action1.action()
		},
		action2Click (index) {
			this.list[index.index].stateModel.action2.action()
		},
		setOrderState (data) {
			data.forEach((it, index) => {
			  switch (it.status) {
			    case 'PAY_STATUS_NO': // 
			      it.stateModel = {
			        orderType: '1',
			        stateTip: '',
			        time: {
			          title: '',
			          time: Math.max(0, (Number(it.timeout_express) - Number(this.serverTime)))
			        },
			        action1: {
			          title: '',
			          show: true,
			          action: () => {
						  uni.showModal({
						  	cancelText:"",
							confirmText: "",
							title: '',
							content: '',
							success: (res) => {
								if (res.confirm) {
									this.$http(this.$urlPath.orderCancel, {
									  ord_id: it.ord_id
									}, '', (result) => {
									  this.$toast('')
									  uni.startPullDownRefresh()
									}, (errorCode, error) => {
									  this.$toast(error)
									})
								}
							}
						  })
			          }
			        },
			        action2: {
			          title: '',
			          show: Number(it.timeout_express) - Number(this.serverTime) > 0,
			          action: () => {
						  this.$push(this.$mRouter.orderInfoPay({
							  no: it.out_trade_no
						  }))
			          }
			        }
			      }
			      break
			    case 'USE_STATUS_NO': // 
			      it.stateModel = {
			        orderType: '2',
			        stateTip: '',
			        time: {
			          title: '',
			          time: it.ord_add_time
			        },
			        action1: this.reBuyTicket(it),
			        action2: {
			          title: it.is_refund === 1 ? '' : '',
			          show: it.is_refund === 1,
			          action: it.is_refund ? () => {
						  this.$push(this.$mRouter.orderRefund({
						  	id: it.ord_id
						  }))
			          } : null
			        }
			      }
			      break
			    case 'NO_COMMENT':
			    case 'USE_STATUS': // 
			      it.stateModel = {
			        orderType: '3',
			        stateTip: '',
			        time: {
			          title: '',
			          time: it.ord_add_time
			        },
			        action1: this.reBuyTicket(it),
			        action2: {
			          title: '',
			          show: true,
			          action: () => {
						  this.$push(this.$mRouter.orderComment({
						  	orderId: it.ord_id
						  }))
			          }
			        }
			      }
			      break
			    case 'USE_STATUS_OFF': // 
			      it.stateModel = {
			        orderType: '6',
			        stateTip: '',
			        time: {
			          title: '',
			          time: it.ord_add_time
			        },
			        action1: {
			          title: '',
			          show: true,
			          action: () => {
						uni.showModal({
							title: '',
							content: '?',
							cancelText: '',
							confirmText: '',
							success: (res) => {
								if (res.confirm) {
									this.$http(this.$urlPath.orderDelete, {
									  ord_id: it.ord_id
									}, '', (result) => {
										this.list.splice(index, 1)
										this.$toast('')
									}, (errorCode, error) => {
									  this.$toast(error)
									})
								} 
							}
						})
			          }
			        },
			        action2: this.reBuyTicket(it)
			      }
			      break
			    case 'USE_STATUS_EXPIRE': // 
			      it.stateModel = {
			        orderType: '7',
			        stateTip: '',
			        time: {
			          title: '',
			          time: it.ord_add_time
			        },
			        action1: {
			          title: '',
			          show: true,
			          action: () => {
			            let confirm = window.confirm('')
			            if (confirm) {
			              this.$http(this.$urlPath.orderDelete, {
			                ord_id: it.ord_id
			              }, '', (result) => {
			                this.reload()
			                this.$toast('')
			              }, (errorCode, error) => {
			                this.$toast(error)
			              })
			            }
			          }
			        },
			        action2: this.reBuyTicket(it)
			      }
			      break
			    case 'ALREADY_COMMENT': // 
			      it.stateModel = {
			        orderType: '5',
			        stateTip: '',
			        time: {
			          title: '',
			          time: it.ord_add_time
			        },
			        action1: this.reBuyTicket(it),
			        action2: {
			          title: '',
			          show: false,
			          action: null
			        }
			      }
			      break
			    case 'USE_STATUS_REVOKE': // 
			      it.stateModel = {
			        orderType: '4',
			        stateTip: '/',
			        time: {
			          title: '',
			          time: it.ord_add_time
			        },
			        action1: this.reBuyTicket(it),
			        action2: {
			          title: '',
			          show: false,
			          action: null
			        }
			      }
			      break
			    case 'USE_STATUS_OVER': // 
			      it.stateModel = {
			        orderType: '9',
			        stateTip: '',
			        time: {
			          title: '',
			          time: it.ord_add_time
			        },
			        action1: this.reBuyTicket(it),
			        action2: {
			          title: '',
			          show: false,
			          action: null
			        }
			      }
			      break
			  }
			})
		}
	}
}